@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@600;800&display=swap);
:root {
  --full-slider-bar: hsl(174, 77%, 80%);
  --slider-background: hsl(174, 86%, 45%);
  --discount-background: hsl(14, 92%, 95%);
  --discount-text: hsl(15, 100%, 70%);
  --CTA-text: hsl(226, 100%, 87%);
  --slider-hover: #7aeadf;
  --slider-active: #24aea1;
  --toggle-hover: var(--slider-hover);
  --horizontal-line: #ecf0fb;

  --white: #ffffff;
  --empty-slider-bar: hsl(224, 65%, 95%);
  --toggle-background: hsl(223, 50%, 87%);
  --text-color: #848ead;
  --title-color: #293356;
  --CTA-background: var(--title-color);
  --box-shadow: #f1f5fe;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; /* 1REM = 10px */
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Manrope", sans-serif;
  font-size: 1.5rem;
  background-image: url(/interactive-pricing-component/static/media/bg-pattern.76d3fa7d.svg);
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-color: #ffffff;
  background-color: var(--white);
}
/* Globals */
h1 {
  margin: 0;
}
p {
  margin: 0;
}

.Header_header__2fIN1 {
  background: url(/interactive-pricing-component/static/media/pattern-circles.9f6ed14b.svg) no-repeat 50%;
  height: 14.5rem;
  margin: 5.7rem 2.4rem 0;
  padding-top: 3.2rem;
  text-align: center;
}
.Header_header__2fIN1 h1 {
  font-size: 2rem;
  font-weight: 800;
  color: var(--title-color);
  margin-bottom: 0.9rem;
}
.Header_header__2fIN1 p {
  color: var(--text-color);
  font-weight: 600;
  font-size: 1.3rem;
  line-height: 2.3rem;
}
.Header_header__2fIN1 p span {
  display: block;
}

/* Desktop */
@media (min-width: 1024px) {
  .Header_header__2fIN1 h1 {
    font-size: 2.8rem;
  }
  .Header_header__2fIN1 p {
    font-size: 1.5rem;
  }
  .Header_header__2fIN1 p span {
    display: inline;
    display: initial;
  }
}

.Main_main__QAuDI {
  background-color: var(--white);
  margin: 3.2rem 2.4rem 0;
  padding: 3.4rem 0 3.1rem;
  border-radius: 0.8rem;
  height: 47.8rem;
  max-width: 54rem;
  box-shadow: 0 3rem 4rem var(--box-shadow);
}
.Main_first-section__1U_KC {
  padding: 0 2.4rem;
}
.Main_first-section__1U_KC .Main_pageviews__GHjRY {
  font-size: 1.2rem;
  font-weight: 800;
  color: var(--text-color);
  text-transform: uppercase;
  letter-spacing: 0.171rem;
  text-align: center;
  margin-bottom: 2.5rem;
}
.Main_input-range-container__3dzRf {
  display: flex;
  flex-direction: column;
}
.Main_input-range__2fXlm {
  -webkit-appearance: none;
  background-color: var(--empty-slider-bar);
  border-radius: 0.5rem;
  width: 100%;
  height: 0.8rem;
  margin: 0;
}
.Main_progress-bar__1kD9s {
  height: 0.8rem;
  background-color: var(--full-slider-bar);
  border-radius: 0.5rem;
  margin: -0.8rem 0 4rem;
  z-index: 1;
}
/* Chrome */
.Main_input-range__2fXlm::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  cursor: pointer;
  /* background-color: var(--full-slider-bar); */
  height: 0.8rem;
  width: 100%;
  border-radius: 0.5rem;
}
.Main_input-range__2fXlm::-webkit-slider-thumb {
  -webkit-appearance: none;
  background-color: var(--slider-background);
  background-image: url(/interactive-pricing-component/static/media/icon-slider.cbca35e0.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  margin-top: calc(-2rem + 0.4rem);
  box-shadow: 0 0.5rem 1.5rem var(--slider-background);
  position: relative;
  z-index: 2;
}
.Main_input-range__2fXlm::-webkit-slider-thumb:hover {
  background-color: var(--slider-hover);
}
.Main_input-range__2fXlm::-webkit-slider-thumb:active {
  cursor: grabbing;
  background-color: var(--slider-active);
}
.Main_first-section__1U_KC .Main_price__2XHli {
  font-size: 3.2rem;
  font-weight: 800;
  color: var(--title-color);
  text-align: center;
  margin-bottom: 3.4rem;
}
.Main_first-section__1U_KC .Main_price__2XHli span {
  font-size: 1.4rem;
  color: var(--text-color);
}
.Main_first-section__1U_KC .Main_billing-options__39dbx {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 2rem;
  margin-bottom: 3.8rem;
}
.Main_first-section__1U_KC .Main_billing-options__39dbx p {
  font-size: 1.2rem;
  color: var(--text-color);
}
/* The switch - the box around the slider */
.Main_switch___pO2j {
  position: relative;
  display: inline-block;
  width: 4.3rem;
  height: 2.2rem;
}

/* Hide default HTML checkbox */
.Main_switch___pO2j input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.Main_slider__3_4EW {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--toggle-background);
  transition: 0.4s;
}

.Main_slider__3_4EW:before {
  position: absolute;
  content: "";
  height: 1.4rem;
  width: 1.4rem;
  left: 0.4rem;
  bottom: 0.4rem;
  background-color: white;
  transition: 0.4s;
}

input:checked + .Main_slider__3_4EW {
  background-color: var(--toggle-hover);
}

input:focus + .Main_slider__3_4EW {
  box-shadow: 0 0 0.1rem var(--toggle-hover);
}

input:checked + .Main_slider__3_4EW:before {
  -webkit-transform: translateX(2.6rem);
  transform: translateX(2.6rem);
  left: 0rem;
}

/* Rounded sliders */
.Main_slider__3_4EW.Main_round__1Zt7q {
  border-radius: 1.5rem;
}

.Main_slider__3_4EW.Main_round__1Zt7q:before {
  border-radius: 50%;
}
.Main_billing-options__39dbx .Main_discount__RS55T {
  background-color: var(--discount-background);
  border-radius: 0.95rem;
  height: 1.9rem;
  width: 4.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Main_billing-options__39dbx .Main_discount__RS55T span {
  display: none;
}
.Main_billing-options__39dbx .Main_discount__RS55T p {
  color: var(--discount-text);
  font-weight: 800;
  text-align: center;
  font-size: 1rem;
}
.Main_main__QAuDI .Main_horizontal-line__35RY6 {
  border-top: 0.1rem solid var(--horizontal-line);
  margin-bottom: 2.4rem;
}
.Main_second-section__wPegG {
  padding: 0 2.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Main_second-section__wPegG .Main_features__5hM5a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  margin-bottom: 3.2rem;
}
.Main_second-section__wPegG .Main_features__5hM5a div {
  display: flex;
  align-items: center;
  grid-column-gap: 1.605rem;
  -webkit-column-gap: 1.605rem;
          column-gap: 1.605rem;
}
.Main_second-section__wPegG .Main_features__5hM5a p {
  display: inline;
  color: var(--text-color);
  font-size: 1.2rem;
}
.Main_second-section__wPegG button {
  background-color: var(--CTA-background);
  color: var(--CTA-text);
  border: none;
  height: 4.1rem;
  width: 17rem;
  border-radius: 2.05rem;
  font-size: 1.2rem;
  font-weight: 800;
  font-family: "Manrope", sans-serif;
}
.Main_second-section__wPegG button:hover {
  cursor: pointer;
  color: var(--white);
}

/* To center the main component */
@media (min-width: 588px) {
  .Main_main__QAuDI {
    margin: 3.2rem auto 0;
  }
}

/* Desktop */
@media (min-width: 1024px) {
  .Main_main__QAuDI {
    padding: 4rem 0 3.1rem;
    height: 39.7rem;
  }
  .Main_first-section__1U_KC {
    padding: 0 4.6rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    grid-row-gap: 2.4rem;
    row-gap: 2.4rem;
  }
  .Main_first-section__1U_KC .Main_pageviews__GHjRY {
    font-size: 1.4rem;
    text-align: left;
    margin-bottom: 0;
  }
  .Main_input-range-container__3dzRf {
    grid-column: 1 / 3;
  }
  .Main_progress-bar__1kD9s {
    margin-bottom: 1.6rem;
  }
  .Main_first-section__1U_KC .Main_price__2XHli {
    font-size: 4rem;
    text-align: right;
    margin-bottom: 0;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }
  .Main_first-section__1U_KC .Main_price__2XHli span {
    font-size: 1.6rem;
  }
  .Main_first-section__1U_KC .Main_billing-options__39dbx {
    align-items: center;
    margin-left: 10.8rem;
    margin-bottom: 4rem;
    grid-column: 1 / 3;
  }
  .Main_billing-options__39dbx .Main_discount__RS55T {
    width: 8rem;
  }
  .Main_billing-options__39dbx .Main_discount__RS55T span {
    display: inline;
    display: initial;
  }
  .Main_main__QAuDI .Main_horizontal-line__35RY6 {
    margin-bottom: 3.2rem;
  }
  .Main_second-section__wPegG {
    padding: 0 4.6rem;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .Main_second-section__wPegG .Main_features__5hM5a {
    align-items: flex-start;
    margin-bottom: 0;
  }
}

